body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background: #ffffff;
}

body::-webkit-scrollbar-thumb {
  background: #EC1D24;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.HeaderContent{
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 0;
    height: 80px;
    align-items: center;
    background-color: white;
   z-index: 1000;
}


.HeaderImgLogo{
    height: 75px;
    width: 120px;
    padding-left: 15px;
}


.HeaderList ul {
    display: flex;
    list-style: none;
}

.HeaderList ul li{
    margin: 0px 40px;
   font-size: 19px;
   padding-top: 10px;
}

.HeaderList ul li a{
    font-family: 'Noto Sans TC', sans-serif;
    text-decoration: none;
    color: black;
    transition: .3s ease-in-out;
    -webkit-transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -ms-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
}

.HeaderList ul li a:hover{
    color: #EC1D24;
}

.HeaderReg a{
    text-decoration: none;
    border: 1px solid #59AB6E;
    padding: 10px;
    margin-right: 10px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: 'Gudea', sans-serif;
    color: rgba(0, 0, 0, 0.575);
}

.HeaderReg i {
    color: #cdd4e3;
    padding-right: 3px;
}


.Bars i{
    font-size: 22px;
    margin-right: 15px;
    
}


.Bars{
    display: none;
    cursor: pointer;
    
}


.LoggedHeaderReg a{
    text-decoration: none;
    padding: 10px 5px;
    margin-right: 10px;
    position: relative;
}

.LoggedHeaderReg i {
    color: #cdd4e3;
    padding-right: 3px;
    font-size: 23px;
}

.Notification{
    position: absolute;
    top: -5px;
    right: -2px;
    background-color: red;
    padding: 0px 5px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 13px;
}


.LogOut{
    border: none;
    outline: none;
    background-color: transparent;
   
}



#Btnn{
    outline: none;
}


@-webkit-keyframes UlAnimate {
  from{
    right: -215px;
  }to{
    right: 0;
  }
}


@keyframes UlAnimate {
  from{
    right: -215px;
  }to{
