        display: none;
    }
    .NewHeaderList ul{
        display: flex;
        flex-direction: column;
        position: absolute;
        text-align: center;
        top: 80px;
        right: -215px;
        background-color: white;
        font-size: 18px;
        list-style: none;
        border-bottom-left-radius: 25px;
