border-radius: 15px;
-webkit-border-radius: 15px;
-moz-border-radius: 15px;
-ms-border-radius: 15px;
-o-border-radius: 15px;
text-align: left;
position: relative;
}

.Messages{
    display: flex;
    flex-direction: column;
    overflow: scroll;
    height: 75vh;
    overflow-x: hidden;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;

}

.Clicka{
    cursor: pointer;
    
}

.Messages p:last-child{
    margin-bottom: 85px;
}


.Messages::-webkit-scrollbar {
    width: 10px;
    
  }
  
  .Messages::-webkit-scrollbar-track {
    background: #ffffff;
    
}
  
  .Messages::-webkit-scrollbar-thumb {
    background: #4285F4;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }


  .ChatRoomContainer{
      padding: 45px 25px;
      display: flex;
      
  }

  .Contacts{
      display: flex;
      flex-direction: column;
      box-shadow: 0px 0px 10px rgba(128, 128, 128, 0.527);
      width: 450px;
      height: 70vh;
      border-radius: 10px;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
      overflow: scroll;
      overflow-x: hidden;
}

.ChatRoomContainer{
    padding-top: 45px;
}

  .Messages p:last-child{
    margin-bottom: 125px;
}


.Contacts::-webkit-scrollbar {
    width: 10px;
  }
  
  .Contacts::-webkit-scrollbar-track {
    background: #ffffff;
  }
  
  .Contacts::-webkit-scrollbar-thumb {
    background: #4285F4;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }

  .Contact{
    display: inline-block;
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
    background-color: rgb(255, 255, 255);
    cursor: pointer;
    margin-right: 5px;
    text-decoration: none;
    padding: 10px 0px;
    color: rgb(0, 0, 0);
    word-break: break-all;
}


.DivOfContacts hr{
    margin-left: 20px;
    margin-right: 20px;
}

.letter{
    margin-top: 15px;
    display: inline-block;
    margin-right: 20px;
    background-color: rgba(34, 34, 34, 0.281);
    color: white;
    padding: 10px 18px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-left: 7px;
}

.lastmessage{
    text-align: right;
    margin-right: 15px;
    font-size: 15px;
    color: rgb(34, 107, 202);
}

.ExistChat{
    width: 100%;
}

.ExistChat div{
    margin: 0;
    padding: 0;
   justify-self: center;
}

.Date{
    position: absolute;
    color: gray;
    right: 0px;
    width: 150px;
    bottom: -20px;
    font-size: 13px;
}

.ADate{
    position: absolute;
    color: gray;
    left: 0px;
    width: 150px;
    bottom: -20px;
    font-size: 13px;
}

@media (max-width: 851px) {
    .ChatRoomContainer{
        flex-wrap: wrap;
        justify-content: center;
        grid-gap: 15px;
        gap: 15px;
    }
}
.H111{
    font-family: 'Bebas Neue', cursive;
    text-align: center;
    padding: 50px 0px;
}

.SearchAlert{
    text-align: center;
    font-size: 25px;
    padding: 25px 0px;
    font-family: 'Montserrat', sans-serif;
    color: gray;
}
